import React from 'react'
import './roofingWorks.scss'
import {
  workOrder,
  materials,
  roofing1,
  roofing2,
  roofing3,
  roofing4,
  roofing5,
  roofing6,
  roofing7,
  roofing8,
  roofing9,
  roofing10,
  roofing11,
  roofing12,
} from '../../assets'
import { Table } from '../../components/Table/Table'
import { AccordionFaq } from '../../components/AccordionFaq/AccordionFaq'
import { OrderTypeSection } from '../../components/OrderTypeSection/OrderTypeSection'
import { Form } from '../../components/Form/Form'
import { Gallery } from '../../components/Gallery/Gallery'
import { TopSliderSection } from '../../components/TopSliderSection/TopSliderSection'

const roofingWorksList = [
  {
    title: ' , ',
    works: ['', '', 'K', "' , ()"],
  },
]

const roofingWorksOrderType = {
  mainTitle: '     ',
  ordersTypeList: [
    {
      title: '',
      subtitle: '  100 2',
      works: ['', ' ', '', '', ' '],
      isTrend: false,
    },
    {
      title: '',
      subtitle: '  190 2',
      works: ['', ' ', '', '', ' '],
      isTrend: true,
    },
    {
      title: '',
      subtitle: '  450 2',
      works: ['', ' ', '', '', ' '],
      isTrend: false,
    },
  ],
}

const roofingWorksFaq = [
  {
    id: 'One',
    question: '        ?',
    answer:
      '    , ,         .               .            -   .           .',
  },
  {
    id: 'Two',
    question: '    ?',
    answer: ' RRGroup     .',
  },
  {
    id: 'Three',
    question: '     ?',
    answer:
      '         ,     .',
  },
]

const roofimgWorksImg = [
  roofing1,
  roofing2,
  roofing3,
  roofing4,
  roofing5,
  roofing6,
  roofing7,
  roofing8,
  roofing9,
  roofing10,
  roofing11,
  roofing12,
]

export const RoofingWorks = () => {
  return (
    <div className="roofing_works">
      <div className="container">
        <div className="page_title">
          <div className="row">
            <div className="col-12">
              <h2> </h2>
            </div>
          </div>
        </div>
        <TopSliderSection animationSlide='roofingWorksSlide' />
        <OrderTypeSection
          title={roofingWorksOrderType.mainTitle}
          content={roofingWorksOrderType.ordersTypeList}
        />
        <section className="work_order">
          <div className="container">
            <h1 className="page_title"> </h1>
            <div className="row">
              <div className="col-sm-12">
                <img src={workOrder} alt="" className="img-fluid w-100" />
              </div>
            </div>
          </div>
        </section>
        <Gallery images={roofimgWorksImg} />
        <AccordionFaq faq={roofingWorksFaq} />
        <section className="materials">
          <div className="container">
            <div className="row">
              <div className="col-12">
                <h3>:</h3>
              </div>
            </div>
            <div className="row">
              <div className="col-sm-12">
                <img src={materials} alt="" className="img-fluid w-100" />
              </div>
            </div>
          </div>
        </section>
        <section className="listWorks">
          <div className="container">
            <div className="row mb-3">
              <div className="col-12">
                <h3>    :</h3>
              </div>
            </div>
            <div className="row">
              {roofingWorksList.map((item) => (
                <Table list={item} />
              ))}
            </div>
          </div>
        </section>
      </div>
    </div>
  )
}
